import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { IntegrationService } from './../../../shared/services/integration.service';
import { FeatureAuthService } from './../../../shared/services/feature-access.service';
import { SubDomainService } from './../../../shared/services/subdomain.service';
import { CookieService } from './../../../shared/services/cookie.service';
import { Integrations } from './../../../shared/models/integrations';
declare var window: any;
declare var jQuery: any;

@Component({
  selector: 'app-og-integrations',
  templateUrl: './integrations.component.html',
  styleUrls: ['./integrations.component.css', './../../../../assets/css/sahil-hover.css', './../../../../assets/css/custom-material.css', './../settings.component.css']
})

export class IntegrationsComponent implements OnInit {
  company: String = '';
  addKey: FormGroup;
  addMarketo: FormGroup;
  mailChimpForm: FormGroup;
  getresponseForm: FormGroup;
  hubspotForm: FormGroup;
  testMarketoForm: FormGroup;
  activeCampaignForm: FormGroup;
  error: boolean = false;
  errorMessage: string;
  isSyncEnableMarketo: boolean = false;
  isSyncEnableSF: boolean = false;
  isSyncEnableGetresponse: boolean = false;
  marketType: string = '';
  pendingLeadsMarketo: string;
  pendingLeadsSF: string;
  pendingLeadsMailchimp: string;
  pendingLeadsGetresponse: string;
  pendingLeadsActiveCampaign: string;
  pendingAweber: string;
  pendingHubspot: string;
  isHubspotConnected: boolean = false;
  isMarketoConnected: boolean = false;
  isActiveCampaignConnected: boolean = false;
  salesforceError: string;
  isSalesforceError: boolean = false;
  isMailChimpError: boolean = false;
  isHubspotError: boolean = false;
  isgetresponseError: boolean = false;
  isSyncEnableMailchimp: boolean = false;
  isSyncEnableActiveCampaign: boolean = false;
  isSyncEnableAweber: boolean = false;
  isSyncEnableHubspot: boolean = false;
  mailChimpErrorMsg: string;
  hubspotErrorMsg: string;
  getresponseErrorMsg: string;
  isActiveCampaignError: boolean = false;
  activeCampaignErrorMsg: string;
  integrationType: string;
  isAppsumoUser: boolean = false;
  integrations: any = new Integrations({});
  activeCampaignIntegrations: any = new Integrations({});
  configuration: any = {
    salesforce: false,
    marketo: false,
    mailchimp: false,
    getresponse: false,
    activeCampaign: false,
    aweber: false,
    hubspot: false
  };
  constructor(private _integrationService: IntegrationService, private fb: FormBuilder,
    private _cookieService: CookieService,
    private _subdomainService: SubDomainService,
    private _featureAuthService: FeatureAuthService) {
    this.company = localStorage.getItem('company');
  }

  ngOnInit() {
    this.getConfiguration();
    this.isSyncEnableMarketo = false;
    this.isSyncEnableSF = false;
    this.getAllConfiguration();
    this.getsyncLead();
    this.addKey = this.fb.group({
      hapikey: ['', Validators.compose([Validators.required])]
    });

    this.addMarketo = this.fb.group({
      client_id: [this.integrations.client_id, Validators.compose([Validators.required])],
      client_secret: [this.integrations.client_secret, Validators.compose([Validators.required])],
      ideantity: [this.integrations.ideantity, Validators.compose([Validators.required])]
    });

    this.mailChimpForm = this.fb.group({
      apiKey: [this.integrations.api_key, Validators.compose([Validators.required])]
    });

    this.hubspotForm = this.fb.group({
      hubspotKey: [this.integrations.hubspotKey, Validators.compose([Validators.required])]
    });

    this.getresponseForm = this.fb.group({
      apiKey: [this.integrations.apiKey, Validators.compose([Validators.required])]
    });

    this.testMarketoForm = this.fb.group({
      firstName: [''],
      email: ['', Validators.compose([Validators.required])],
      lastName: ['']
    });

    this.activeCampaignForm = this.fb.group({
      apiKey: [this.activeCampaignIntegrations.api_key, Validators.compose([Validators.required])],
      account: [this.activeCampaignIntegrations.account, Validators.compose([Validators.required])],
    });

    /* let storage = JSON.parse(this._cookieService.readCookie('storage'));
     if(storage.company.is_appsumo_created === true && storage.company.billing.chargebee_plan_id==='appsumo_d'){
       this.isAppsumoUser = true ;
       jQuery('.dc-mailchimp').addClass('dc-mailchimp-style');
     }else{
       this.isAppsumoUser = false ;
     }*/
    jQuery.material.init();
  }

  connectSalesforce() {
    jQuery('#mapping-int-toAccess').modal('hide');
    let data = {};
    this._integrationService.getLink(data, 'salesforce')
      .subscribe((response) => {
        let companyId: string = <string>this._subdomainService.subDomain.company_id;
        this._cookieService.createCookie('comp', companyId, 3);
        let newWindow = window.open(response, 'GoogleWindow', 'width=600, height=500,scrollbars=yes');
        let self = this;
        let interval = setInterval(function () {
          let cookie = self._cookieService.readCookie('comp');
          if (cookie === 'success') {
            newWindow.close();
            self.getAllConfiguration();
            window.toastNotification('You have Successfully added salesforce for integration.');
            clearInterval(interval);
          } else if (cookie === null) {
            clearInterval(interval);
          }
        }, 5000);
      }, (error) => {
        //console.log(error);
      });
  }

  /*  connectMailChimp() {
        let data = {} ;
         this._integrationService.getLink(data, 'mailchimp')
         .subscribe((response) => {
              let companyId: string = <string>this._subdomainService.subDomain.company_id;
              this._cookieService.createCookie('comp', companyId , 3);
              let newWindow = window.open(response, 'GoogleWindow', 'width=600, height=500,scrollbars=yes');
              let self = this;
              let interval = setInterval(function(){
                  let cookie = self._cookieService.readCookie('comp');
                  if (cookie === 'success') {
                      newWindow.close();
                      self.getAllConfiguration();
                      window.toastNotification('You have Successfully added salesforce for integration.');
                      clearInterval(interval);
                  }else if (cookie === null) {
                        clearInterval(interval);
                  }
              }, 5000);
         }, (error) => {
             console.log(error);
         });
    }*/
  getAllConfiguration() {
    this._integrationService.getAllConfiguration()
      .subscribe((result) => {
        this.configuration.salesforce = result.salesforce ? result.salesforce : false;
        this.configuration.marketo = result.marketo ? result.marketo : false;
        this.configuration.mailchimp = result.mailchimp ? result.mailchimp : false;
        this.configuration.getresponse = result.getresponse ? result.getresponse : false;
        this.configuration.activeCampaign = result.active_campaign ? result.active_campaign_config : false;
        this.configuration.aweber = result.aweber ? result.aweber : false;
        this.configuration.hubspot = result.hubspot ? result.hubspot : false;
      }, (error) => {
        //console.log(error);
      });
  }

  getConfiguration() {
    this._integrationService.getConfiguration()
      .subscribe((result) => {
        if (result.marketo_config) {
          this.integrations.client_id = result.marketo_config.client_id;
          this.integrations.client_secret = result.marketo_config.client_secret;
          this.integrations.ideantity = result.marketo_config.instance_url;
        }
        if (result.mailchimp_config) {
          this.integrations.api_key = result.mailchimp_config.api_key;
        }
        if (result.getresponse_config) {
          this.integrations.apiKey = result.getresponse_config.api_key;
        }
        if (result.hubspot_config) {
          this.integrations.hubspotKey = result.hubspot_config.api_key;
        }
        if (result.active_campaign_config) {
          this.activeCampaignIntegrations.api_key = result.active_campaign_config.api_key;
          this.activeCampaignIntegrations.account = result.active_campaign_config.instance_url;
        }

        console.log(this.integrations, '-------------------------------------------------------------');
      }, (error) => {
        //console.log(error);
      });
  }

  getsyncLead() {
    this.isSyncEnableMarketo = false;
    this.isSyncEnableSF = false;
    this.isSyncEnableActiveCampaign = false;
    this._integrationService.getCompanySyncLeads()
      .subscribe((result) => {
        console.log("@@@@@@@@@@@@@@@@@@@", result);
        this.pendingLeadsSF = result.salesforce + ' item';
        this.pendingLeadsMarketo = result.marketo + ' item';
        this.pendingLeadsMailchimp = result.mailchimp + ' item';
        this.pendingLeadsGetresponse = result.getresponse + 'item';
        this.pendingLeadsActiveCampaign = result.active_campaign + ' item';
        this.pendingAweber = result.aweber + ' item';
        this.pendingHubspot = result.hubspot + 'item';
        if (result.marketo >= 1) {
          this.isSyncEnableMarketo = true;
        }
        if (result.salesforce >= 1) {
          this.isSyncEnableSF = true;
        }
        if (result.mailchimp >= 1) {
          this.isSyncEnableMailchimp = true;
        }
        if (result.getresponse >= 1) {
          this.isSyncEnableGetresponse = true;
        }
        if (result.active_campaign >= 1) {
          this.isSyncEnableActiveCampaign = true;
        }
        if (this.pendingAweber >= '1') {
          this.isSyncEnableAweber = true;
        }
        if (result.hubspot >= 1) {
          this.isSyncEnableHubspot = true;
        }
        console.log(this.isSyncEnableHubspot, 'iiiiiiiiiiiiiiiiiiiiiiiiiiiiii',result);

      }, (error) => {
        //console.log(error);
      });
  }

  syncLead(type: string) {
    this.integrationType = type;
    let self = this;
    if (type === 'marketo') {
      jQuery('.sync-leads-marketo').html('Please Wait..');
    }
    if (type === 'salesforce') {
      jQuery('.sync-leads-sf').html('Please Wait..');
    } if (type === 'mailchimp') {
      jQuery('.sync-leads-mailchimp').html('Please Wait..');
    }
    if (type === 'getresponse') {
      jQuery('.sync-leads-getresponse').html('Please Wait..');
    }
    if (type === 'active_campaign') {
      jQuery('.sync-leads-activeCampaign').html('Please Wait..');
    }
    this._integrationService.syncLeads(type)
      .subscribe((result) => {
        if (result.success) {
          //console.log(result,'*********************');
          if (type === 'marketo') {
            self.isSyncEnableMarketo = false;
            jQuery('.sync-leads-marketo').html('Sync');
          }
          if (type === 'salesforce') {
            self.isSyncEnableSF = false;
            jQuery('.sync-leads-sf').html('Sync');
          } if (type === 'mailchimp') {
            self.isSyncEnableSF = false;
            jQuery('.sync-leads-mailchimp').html('Sync');
          }
          if (type === 'getresponse') {
            self.isSyncEnableGetresponse = false;
            jQuery('.sync-leads-getresponse').html('Sync');
          }
          if (type === 'active_campaign') {
            this.isSyncEnableActiveCampaign = false;
            jQuery('.sync-leads-activeCampaign').html('Sync');
          }
          if (type === 'hubspot') {
            this.isSyncEnableActiveCampaign = false;
            jQuery('.sync-leads-hubspot').html('Sync');
          }
          self.getsyncLead();
          window.toastNotification('Leads will be synced to connected ' + type + ' for this calculator');
        } else {
          if (type === 'marketo') {
            self.isSyncEnableMarketo = true;
            jQuery('.sync-leads-marketo').html('Sync (' + self.pendingLeadsMarketo + ')');
          }
          if (type === 'salesforce') {
            self.isSyncEnableSF = true;
            jQuery('.sync-leads-sf').html('Sync (' + self.pendingLeadsSF + ')');
          } if (type === 'mailchimp') {
            self.isSyncEnableSF = true;
            jQuery('.sync-leads-mailchimp').html('Sync (' + self.pendingLeadsMailchimp + ')');
          }
          if (type === 'getresponse') {
            self.isSyncEnableGetresponse = true;
            jQuery('.sync-leads-getresponse').html('Sync (' + self.pendingLeadsGetresponse + ')');
          }
          if (type === 'active_campaign') {
            this.isSyncEnableActiveCampaign = true;
            jQuery('.sync-leads-activeCampaign').html('Sync (' + this.pendingLeadsActiveCampaign + ')');
          }
           if (type === 'hubspot') {
            this.isSyncEnableActiveCampaign = true;
            jQuery('.sync-leads-hubspot').html('Sync (' + this.pendingHubspot + ')');
          }
          jQuery('#syncError-modal').modal('show');
        }
      }, (error) => {
        if (type === 'marketo') {
          self.isSyncEnableMarketo = true;
          jQuery('.sync-leads-marketo').html('Sync (' + self.pendingLeadsMarketo + ')');
        }
        if (type === 'salesforce') {
          self.isSyncEnableSF = true;
          jQuery('.sync-leads-sf').html('Sync (' + self.pendingLeadsSF + ')');
        } if (type === 'mailchimp') {
          self.isSyncEnableSF = true;
          jQuery('.sync-leads-mailchimp').html('Sync (' + self.pendingLeadsMailchimp + ')');
        }
        if (type === 'getresponse') {
          self.isSyncEnableGetresponse = true;
          jQuery('.sync-leads-getresponse').html('Sync (' + self.pendingLeadsGetresponse + ')');
        }
        if (type === 'active_campaign') {
          this.isSyncEnableActiveCampaign = true;
          jQuery('.sync-leads-activeCampaign').html('Sync (' + this.pendingLeadsActiveCampaign + ')');
        }
         if (type === 'hubspot') {
          this.isSyncEnableActiveCampaign = true;
          jQuery('.sync-leads-hubspot').html('Sync (' + this.pendingHubspot + ')');
        }
        jQuery('#syncError-modal').modal('show');
      });
  }

  openModal() {
    jQuery('#add-new-hapikey').modal('show');
  }
  openMarketo() {
    if (!this._featureAuthService.features.integrations['marketo']) {
      return;
    }
    jQuery('#add-marketo').modal('show');
  }
  /* openSalesforce() {
     if(!this._featureAuthService.features.integrations['salesforce']){
       return;
     }
     jQuery('#mapping-int-toAccess').modal('show');
     this.integrationType = 'salesforce';
   }*/
  openAuthorization(type: string) {
    if (!this._featureAuthService.features.integrations[type]) {
      return;
    }
    jQuery('#mapping-int-toAccess').modal('show');
    this.integrationType = type;
  }
  openMailChimp() {
    if (!this._featureAuthService.features.integrations['mailchimp']) {
      return;
    }
    jQuery('#add-mailchimp').modal('show');
  }
  openGetResponse() {
    if (!this._featureAuthService.features.integrations['get_response']) {
      return;
    }
    jQuery('#add-getresponse').modal('show');
  }

  openHubspot() {
    if (!this._featureAuthService.features.integrations['hubspot']) {
      return;
    }
    jQuery('#add-hubspot').modal('show');

  }
  connectMarketo() {
    if (!this._featureAuthService.features.integrations['marketo']) {
      return;
    }
    jQuery('#btnmMarketo').html('please Wait...').attr('disabled', true);
    let data = {
      client_id: this.addMarketo.value.client_id,
      client_secret: this.addMarketo.value.client_secret,
      restEndpoint: this.addMarketo.value.ideantity,
      company: this.company
    };
    this._integrationService.authorization(data, 'marketo')
      .subscribe((response) => {
        if (response.errors) {
          this.errorMessage = 'Incorrect Endpoints. Please re-enter correct endpoint for Marketo.';
          this.error = true;
          jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
        } else if (response.error) {
          if (response.error === 'unauthorized') {
            this.errorMessage = 'Invalid Credentials - Please re-enter your Marketo Client Id';
          } else if (response.error === 'invalid_client') {
            this.errorMessage = 'Invalid Credentials - Please re-enter your Marketo Client Secret';
          } else {
            this.errorMessage = response.error_description;
          }
          this.error = true;
          jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
        } else {
          this.error = false;
          jQuery('#btnmMarketo').html('Reconfigure Marketo').attr('disabled', false);
          this.configuration.marketo = true;
          jQuery('#add-marketo').modal('hide');
          window.toastNotification('You have Successfully added Marketo for integration, Please test it by clicking on Test Connection.');
        }
      }, (error) => {
        if (error.error.code === 'ENOTFOUND' || error.error.code === 'E_UNEXPECTED') {
          this.errorMessage = 'Authentication error - Please re-enter the end points';
        } else {
          this.errorMessage = error.error.message;
        }
        this.error = true;
        jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
      });
  }
  connectMailChimp() {
    if (!this._featureAuthService.features.integrations['mailchimp']) {
      return;
    }
    jQuery('#btnMailChimp').html('Please Wait...');
    let data = {
      api_key: this.mailChimpForm.value.apiKey,
      company: this.company
    };
    let self = this;
    self._integrationService.authorization(data, 'mailchimp')
      .subscribe((response) => {
        console.log(response.status, 'status');
        if (response.status === 401) {
          self.isMailChimpError = true;
          jQuery('#btnMailChimp').html('Connect');
          self.mailChimpErrorMsg = 'Invalid Credentials - Please re-enter your Mailchimp Api key';
        } if (response.account_id) {
          jQuery('#add-mailchimp').modal('hide');
          jQuery('#btnMailChimp').html('Connect');
          window.toastNotification('You have successfully added Mailchimp for integration.');
          self.getAllConfiguration();
        }
      }, (error) => {
        self.isMailChimpError = true;
        jQuery('#btnMailChimp').html('Connect');
        if (error.error.code === 'ENOTFOUND') {
          self.mailChimpErrorMsg = 'Invalid Credentials - Please re-enter your Mailchimp Api key';
        }
      });
  }
  connectGetResponse() {
    if (!this._featureAuthService.features.integrations['get_response']) {
      return;
    }
    jQuery('#btngetresponse').html('Please Wait...');
    let data = {
      api_key: this.getresponseForm.value.apiKey,
      company: this.company
    };
    let self = this;
    self._integrationService.authorization(data, 'getresponse')
      .subscribe((response) => {
        console.log(response, 'status');
        if (response.httpStatus === 401) {
          self.isgetresponseError = true;
          jQuery('#btngetresponse').html('Connect');
          self.getresponseErrorMsg = 'Invalid Credentials - Please re-enter your GetResponse Api key';
        } if (response.accountId) {
          jQuery('#add-getresponse').modal('hide');
          jQuery('#btngetresponse').html('Connect');
          window.toastNotification('You have successfully added GetResponse for integration.');
          self.getAllConfiguration();
        }
      }, (error) => {
        self.isgetresponseError = true;
        jQuery('#btngetresponse').html('Connect');
        if (error.error.code === 'ENOTFOUND') {
          self.getresponseErrorMsg = 'Invalid Credentials - Please re-enter your GetResponse Api key';
        }
      });
  }

  connectHubspot() {
    let data = {
      api_key: this.hubspotForm.value.hubspotKey,
      company: this.company
    };
    let self = this;
    jQuery('#btnHubspot').html('Connecting...');
    self._integrationService.authorization(data, 'hubspot')
      .subscribe((response) => {
        if (response.status === 'error') {
          self.isHubspotError = true;
          self.isHubspotConnected = false;
          jQuery('#btnHubspot').html('Connect');
          self.hubspotErrorMsg = 'Invalid Credentials - Please re-enter your Hubspot Api key';
        }else{
          self.isHubspotError = false;
          self.isHubspotConnected = true;
          jQuery('#add-hubspot').modal('hide');
          jQuery('#btnHubspot').html('Connect');
          window.toastNotification('You have successfully added Hubspot for integration.');
          self.getAllConfiguration();
        }
      }, (error) => {
        self.isMailChimpError = true;
        jQuery('#btnHubspot').html('Connect');
        if (error.error.code === 'ENOTFOUND') {
          self.mailChimpErrorMsg = 'Invalid Credentials - Please re-enter your Hubspot Api key';
        }
      });
  }
  change() {
    this.error = false;
  }
  focus() {
    this.isMailChimpError = false;
    this.mailChimpErrorMsg = '';
  }
  openPopup(type) {
    let featureType = type.split('-').join('_');
    if (!this._featureAuthService.features.integrations[featureType]) {
      return;
    }
    jQuery('#' + type).modal({
      cache: false
    }, 'show');
    this.integrationType = featureType;
  }

  addIsfocusedKey(id) {
    jQuery('#' + id).addClass('is-focused');
    switch (id) {
      case "activeCampaignKey":
        this.isActiveCampaignError = false;
        this.activeCampaignErrorMsg = '';
        break;
      default:
        // code...
        break;
    }
  }


  removeIsfocusedKey(id) {
    jQuery('#' + id).removeClass('is-focused');
  }

  connectActiveCampaign() {
    if (!this._featureAuthService.features.integrations['active_campaign']) {
      return;
    }
    this.isActiveCampaignError = false;
    jQuery('#btnmActiveCampaign').html('please Wait...').attr('disabled', true);
    let data = {
      key: this.activeCampaignForm.value.apiKey,
      account: this.activeCampaignForm.value.account
    };
    this._integrationService.authorization(data, 'active_campaign')
      .subscribe((response) => {
        if (response.success) {
          this.configuration.activeCampaign = response;
          jQuery('#active-campaign').modal('hide');
          jQuery('#btnmActiveCampaign').html('Connect').attr('disabled', false);
        }
        else {
          this.activeCampaignErrorMsg = 'Please Enter Your Correct API Key or Account Name ';
          this.isActiveCampaignError = true;
          jQuery('#btnmActiveCampaign').html('Connect').attr('disabled', false);
        }
      },
      (error) => {
        this.activeCampaignErrorMsg = 'Please Enter Your Correct API Key or Account Name ';
        this.isActiveCampaignError = true;
        jQuery('#btnmActiveCampaign').html('Connect').attr('disabled', false);
      })
  }

  connectAweber() {
    jQuery('#mapping-int-toAccess').modal('hide');
    let data = {};
    this._integrationService.getLink(data, 'aweber')
      .subscribe((response) => {
        let companyId: string = <string>this._subdomainService.subDomain.company_id;
        this._cookieService.createCookie('comp', companyId, 3);
        let newWindow = window.open(response, 'GoogleWindow', 'width=600, height=500,scrollbars=yes');
        let self = this;
        let interval = setInterval(function () {
          let cookie = self._cookieService.readCookie('comp');
          console.log(response, 'cookei valueeeeeeeeeeeeeeeeeeeeeeeeeeeee', cookie);
          if (cookie === 'success') {
            newWindow.close();
            self.getAllConfiguration();
            //jQuery('#mapping-int-success').modal('show');
            window.toastNotification('You have successfully added aweber for integration, Please test it by clicking on Test Connection.');
            clearInterval(interval);
          } else if (cookie === null) {
            clearInterval(interval);
            newWindow.close();
          }
        }, 5000);
      }, (error) => {
        console.log(error);
      });
  }

  checkFeatureAccess(type: string) {
    let self = this;
    let available: Boolean = this._featureAuthService.features.integrations[type];
    if (!available) {
      jQuery('div:not(#premiumModal)').modal('hide');
      self._featureAuthService.setSelectedFeature('integrations');
      jQuery('.integrations').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
      jQuery('#analyticsRef').attr('active', false);
    }
  }

  returnFeatureAccess(type: string) {
    return this._featureAuthService.features.integrations[type];
  }

  // gethubSpot() {
  //      this._integrationService.geHubspotData(this.company)
  //      .subscribe((response) => {
  //         this.isHubspotConnected = true;
  //      }, (error) => {
  //          console.log(error);
  //      });
  // }

  premiumPopup(feature: string) {
    this._featureAuthService.setSelectedFeature(feature);
    jQuery('.' + feature).addClass('activegreen limited-label');
    jQuery('#premiumModal').modal('show');
    jQuery('.modal-backdrop').insertAfter('#premiumModal');
  }


}





